@import url("https://fonts.googleapis.com/css?family=Pacifico&display=swap");

.App {
      background-image: url('./assets/land.png'); 
      background-size: 100% 100%;
      background-position: center;
      /* display: flex; */
    justify-items: center;
    align-items: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.coming{
  color: black;
  opacity: 0.7;
  display: flex;
  flex-direction: column;
}
.logo{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.start{
    display: flex;
    flex-direction: row;
    justify-items: center;
    align-items: center
}
.font{
  font-family: 'Pacifico', cursive;
  font-size: 40px;
  font-weight: bold;
  text-align: center;
}

.card{
  justify-self: center;
  padding: 50px;
  margin: 0 10px;
  background: rgba(0, 0, 0, 0.116);
  border-radius: 15%;
}
@media (max-width:500px){
  .card{
    padding: 20px;
  margin: 0 10px;
  }
  .font{
    font-size: 1.2em;
  }
}